import React, { useState } from 'react'
import { VscClose } from 'react-icons/vsc'
import { FilterButton, FilterWrapper } from './style'

interface filterprops {
  items: string[]
  onFilter: (item: string) => void
}

export default function FilterSet(props: filterprops) {
  const [filter, setFilter] = useState('')
  const filterBy = (catName: string) => {
    if (filter === catName) {
      setFilter('')
      props.onFilter('')
    } else {
      setFilter(catName)
      props.onFilter(catName)
    }
  }

  return (
    <FilterWrapper>
      <FilterButton key="clear" isActive={false} onClick={() => setFilter('')}>
        <VscClose /> Clear All
      </FilterButton>
      {props.items.map((filterStr) => (
        <FilterButton key={filterStr} isActive={filter === filterStr} onClick={() => filterBy(filterStr)}>
          {filterStr}
        </FilterButton>
      ))}
    </FilterWrapper>
  )
}
