import styled from 'styled-components/macro'

interface theProps {
  isCompact?: boolean
}

export const IconButton = styled.div`
  width: var(--accessory-button-height);
  display: flex;
  justify-content: center;
  align-items: center;
  border-left: 1px solid var(--grey-1x);
  cursor: pointer;
  font-size: 22px;
  color: var(--grey-3);
  flex-shrink: 0;

  &:hover {
    background-color: var(--grey-1);
  }
`

export const ThumbnailWrapper = styled.div`
  width: var(--accessory-button-height);
  background-color: var(--grey-2);
  flex-shrink: 0;
  cursor: pointer;
`

export const MiniCardWrapper = styled.div<theProps>`
  margin-bottom: 5px;
  border: 1px solid var(--grey-2);
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.07);
  background-color: white;
  display: flex;
  align-items: center;
  height: ${(p) => (p.isCompact ? 'calc( var(--accessory-button-height) * 0.8)' : 'var(--accessory-button-height)')};
  &:hover {
    box-shadow: 1px 4px 6px rgba(0, 0, 0, 0.15);
  }

  ${IconButton}, ${ThumbnailWrapper} {
    height: ${(p) => (p.isCompact ? 'calc( var(--accessory-button-height) * 0.8)' : 'var(--accessory-button-height)')};
  }
`
export const AddIcon = styled.div`
  display: flex;
  color: var(--confirm);
`
export const NaIcon = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: var(--grey-4);
`

export const RemoveIcon = styled.div`
  display: flex;
  color: var(--grey-3);
`
export const AddButton = styled(IconButton)`
  /* ${MiniCardWrapper}:hover & path {
    fill: var(--accent-dark);
  } */
  &:hover ${RemoveIcon} path,
  &:hover ${RemoveIcon} svg,
  &:hover ${RemoveIcon} {
    color: var(--warning);
    fill: var(--warning);
  }
  &:hover ${RemoveIcon} svg {
    transition: transform 300ms;
    transform: rotate(90deg) scale(1.5);
  }

  &:hover path,
  &:hover svg,
  &:hover {
    color: var(--confirm);
    fill: var(--confirm);
  }
`
export const Thumbnail = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
`

export const ContentWrapper = styled.div`
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
`

export const Subtitle = styled.div`
  font-size: 18px;
  font-weight: 700;
  color: black;
  line-height: 18px;
  /* color: var(--grey-3); */
  display: block;
  text-decoration: none;
`

interface alignProps {
  hasSubtitle?: boolean
}

export const Title = styled.span<alignProps>`
  font-size: 15px;
  font-weight: 600;
  line-height: 15px;
  display: block;
  grid-column: 1 / 3;
  align-self: ${(p) => (p.hasSubtitle ? 'self-end' : 'center')};
  color: var(--grey-7);
`
export const MoreInfo = styled.span`
  color: var(--grey-3);
  text-align: right;
  font-size: 14px;
  line-height: 18px;
  font-weight: 300;
`
export const Content = styled.div`
  display: grid;
  gap: 5px;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  align-items: self-start;
  flex-grow: 1;
  padding: 0 10px;
  cursor: pointer;
  height: 100%;
`
