import styled from 'styled-components/macro'

interface FilterProps {
  readonly isActive: boolean
}

export const FilterButton = styled.button<FilterProps>`
  border-radius: 100px;
  padding: 5px 15px;
  margin: 3px 6px;
  font-size: 13px;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  background-color: transparent;
  color: ${(p) => (p.isActive ? 'var(--accent-dark)' : 'var(--grey-3)')};
  border: 1px solid ${(p) => (p.isActive ? 'var(--text)' : 'var(--grey-2)')};

  &:hover {
    color: ${(p) => (p.isActive ? 'var(--accent-dark)' : 'var(--grey-5)')};
    border: 1px solid ${(p) => (p.isActive ? 'var(--accent-dark)' : 'var(--grey-5)')};
  }
`

export const FilterWrapper = styled.div`
  margin-left: -6px;
  margin-right: -6px;
  margin-bottom: 14px;
`
