import { skipToken } from '@reduxjs/toolkit/dist/query'
import { PrefetchOptions } from '@reduxjs/toolkit/dist/query/core/module'
import { useEffect } from 'react'
import { useDispatch } from 'react-redux'
import { strapiApi } from '../store/middleware/strapiApi'

type EndpointNames = keyof typeof strapiApi.endpoints

export function usePrefetch<T extends EndpointNames>(
  endpoint: T,
  arg: Parameters<typeof strapiApi.endpoints[T]['initiate']>[0] | typeof skipToken,
  options: PrefetchOptions = {},
) {
  const dispatch = useDispatch()
  useEffect(() => {
    if (arg !== skipToken) {
      dispatch(strapiApi.util.prefetch(endpoint, arg as any, options))
    }
  }, [arg])
}
