import React, { MouseEventHandler } from 'react'
import { VscAdd, VscCheck, VscClose } from 'react-icons/vsc'
import { truncate } from '../../../utils/utils'
import {
  AddButton,
  AddIcon,
  Content,
  ContentWrapper,
  MiniCardWrapper,
  MoreInfo,
  NaIcon,
  RemoveIcon,
  Subtitle,
  Thumbnail,
  ThumbnailWrapper,
  Title,
} from './style'

interface MiniCardProps {
  image: string | undefined
  onDetailsClick: MouseEventHandler<HTMLDivElement>
  onAddClick: MouseEventHandler<HTMLDivElement>
  onRemoveClick: MouseEventHandler<HTMLDivElement>
  title: string
  description?: string
  isAdded: boolean
  showChevron?: boolean | undefined
  showButton?: boolean | undefined
  isCompact?: boolean | undefined
  iconRemove?: boolean
  hasMoreInfo?: boolean
}
export default function MiniCard(props: MiniCardProps) {
  const buttonType = props.iconRemove ? (
    <RemoveIcon>
      <VscClose />
    </RemoveIcon>
  ) : (
    <AddIcon>
      <VscCheck />
    </AddIcon>
  )

  const icon = props.isAdded ? (
    <AddButton onClick={props.onRemoveClick}>{buttonType}</AddButton>
  ) : (
    <AddButton onClick={props.onAddClick}>
      <VscAdd />
    </AddButton>
  )

  return (
    <MiniCardWrapper isCompact={props.isCompact}>
      <ThumbnailWrapper onClick={props.onDetailsClick}>
        {props.image ? (
          <Thumbnail src={'' + process.env.REACT_APP_ASSETS_URL + props.image} alt="" />
        ) : (
          <NaIcon>N/A</NaIcon>
        )}
      </ThumbnailWrapper>
      <ContentWrapper>
        <Content title={props.title} onClick={props.onDetailsClick}>
          <Title hasSubtitle={!!props.description}>{truncate(props.title, 40)}</Title>
          {props.description && (
            <Subtitle>
              {props.description} {props.hasMoreInfo && <MoreInfo>More Info</MoreInfo>}
            </Subtitle>
          )}
        </Content>
        {props.showButton && icon}
      </ContentWrapper>
    </MiniCardWrapper>
  )
}
